.testContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.testType {
    font-size: 40px;
    font-weight: 800;
    text-align: center;
}

.testTitle {
    font-size: 20px;
    font-weight: 800;
    text-align: center;
    background-color: #43ea80;
    color: white;
    margin: auto;
    padding: 5px;
    border-radius: 5px;
}

.largebutton {
    background-image: -webkit-linear-gradient(right, #43ea80, #17fa7d);
    border: none;
    margin: 10px auto;
    font-size: 30px;
    font-weight: bold;
    padding: 5px 20px;
    border-radius: 60px;
    color: white;
    text-align: center;
    align-items: center;
    display: block;
}

.score {
    margin: 30px auto;
    text-align: center;
    font-size: 50px;
}

.largebutton:hover {
    cursor: pointer;
    background-image: none;
    background-color: black;
    color: white
}

.letterScoreTable {
    margin: auto;
    text-align: center;
    width: 40%;
}

.score {
    margin: 30px auto;
    text-align: center;
    font-size: 50px;

}

.letterScoreTable tr:nth-child(even) {
    background-color: rgb(240, 240, 240);
}

.sentenceScoreTable {
    margin: auto;
    text-align: left;
    width: 40%;
}

.sentenceScoreTable td {
    padding: 10px;
}

.sentenceScoreTable tr:nth-child(even) {
    background-color: rgb(240, 240, 240);
}

.optionlist{
    list-style-type: none;
    margin:20px auto;
}
.label{
    margin-left:30px;
    font-size:20px
}

.optionBox{
    margin-left:25%;
    margin-top: 20px;

    border-radius:10px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.interestoptionBox{
        margin-left: 20%;
        margin-top: 20px;

        border-radius: 10px;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    }

.optionBox label,.interestoptionBox label{
    padding-left:10px;
}

.optionBox tr:nth-child(even) {
    background-color: rgb(240, 240, 240);
}
.interestoptionBox tr:nth-child(even) {
    background-color: rgb(240, 240, 240);
}

.optionBox td, .interestoptionBox td{
    padding:10px;
    border:none;
    margin:0;
}

.instructionbox{
    margin:10px 25%;
}

.resultBox{
    margin:auto;
    padding:20px;
    border-radius: 20px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.resultBox tr:nth-child(even) {
    background-color: rgb(240, 240, 240);
}

.resultBox td{
    padding:10px;
}

.resultBox h1{
    text-align: center;
}
@media only screen and (max-width: 768px){
    .optionBox,.interestoptionBox{
        margin-left:0%;
    }
        .instructionbox{
            margin-left:5%;
        }
}