.introContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.questionContainer{
    background-color: rgb(235, 235, 235);
    margin:10px auto;
    border-radius: 30px;
    padding:20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    width: 60%;
}

.hint{
    color:darkgrey;
}

.speechContainer {
    display: flex;
    flex-direction: column;
    /* align-items: flex-start; */

}

.largebutton{
    background-image: -webkit-linear-gradient(right, #43ea80, #17fa7d);
        border: none;
        margin: 10px auto;
        text-align:center;
        font-size: 35px;
        font-weight: bold;
        padding: 10px;
        border-radius: 30px;
        color: white;
}


.introbutton,.submitButton {
    background-image: -webkit-linear-gradient(right, #43ea80, #17fa7d);
    border: none;
    margin: 10px;
    font-size: 15px;
    font-weight: bold;
    padding: 10px;
    border-radius: 30px;
    color: white;
}

.introbutton:hover,.largebutton:hover,.submitButton:hover {
    cursor: pointer;
    background-image: none;
    background-color: black;
    color: white
}

.introbutton:disabled,.submitButton:disabled {
    background-image: none;
    background-color: grey;
}
.submitButton{
    margin:10px auto;
}

.transcript {
    text-align: left;
    font-weight: 500;
}


.questionContainer p{
    font-size: 20px;
}

@media only screen and (max-width: 1100px) {
    .questionContainer {
        margin: 10px auto;
        width: 80%;
    }
}