.testContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.testType{
    font-size: 60px;
    font-weight: 800;
    text-align: center;
}
.testTitle{
    font-size: 20px;
    font-weight: 800;
    text-align: center;
    background-color: #43ea80;
    color:white;
    margin:auto;
    padding:5px;
    border-radius: 5px;
}
.largebutton{
    background-image:  -webkit-linear-gradient(right, #43ea80, #17fa7d);
        border: none;
        margin:10px auto;
        font-size: 30px;
        font-weight: bold;
        padding: 5px 20px;
        border-radius: 60px;
        color: white;
        text-align: center;
        align-items: center;
        display:block;
}
.score{
    margin:30px auto;
    text-align: center;
    font-size: 50px;
}
.largebutton:hover {
    cursor: pointer;
    background-image: none;
    background-color: black;
    color: white
}
.letterScoreTable{
    margin:auto;
    text-align: center;
    width:40%;
}


.letterScoreTable tr:nth-child(even){
    background-color:rgb(240, 240, 240);
}


.instructionbox {
    margin: 10px 35%;
}

@media only screen and (max-width: 768px) {
    .instructionbox {
        margin-left: 5%;
    }
}
