


.profileDiv{
    display:flex;
    justify-content: center;
    flex-direction:column;
}

.profile{
    text-align:center;
}

.profileTable{
    margin:10px auto;
    background-color:#96f7e5;
    padding:10px;
    border-radius:20px;
    
}

.bold{
    font-weight:bold;
}


.testHeading{
    text-align:center;
    margin-top: 50px;
}

.testList{
    margin:auto;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding:10px;
    width:50vw;
    text-align: center;
}



.testList tbody tr{
    padding:10px auto;
}

.testList tbody tr:hover{
    cursor:pointer;
    background-color:rgb(228, 227, 227);
}

.testList tr td{
    padding:10px 90px;
    border-bottom: 1px solid black;
}

@media only screen and (max-width:768px) {
    .testList {
        width: 90vw;
    }
        .testList tr td{
            padding:5px 5px;
        }
}

