.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* flex-shrink: 2; */
    margin-bottom: 30px;
}


.loginbox {
    /* margin:0px 250px; */
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1000px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    overflow: hidden;
}

.leftpanel {
    width: 60em;
    height: 760px;
    /* background-image: linear-gradient(to left, #43ea80, #38f8d4); */
    background-color: #43ea80;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    /* flex-shrink: 3; */

}

.tagline {
    font-family: 'Poppins';
    font-size: 30px;
    color: white;
    text-align: center;
}

.rightpanel {
    margin: 50px 100px;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.help,
.forgot,
.signup {
    text-decoration: none;
    text-align: right;
    font-family: 'Poppins';
}

.rightpanel h1 {
    text-align: center;
    font-size: 60px;
    margin-top: 40px;
}

.inputs {
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    align-items: center;
}

.inputs input[type="text"],
.inputs input[type="email"],
.inputs input[type="password"] {
    border: 1px solid rgb(212, 212, 212);
    border-radius: 4px;
    padding: 10px;
    margin: 10px;
    width: 300px;
    font-family: 'Poppins';
    font-size: 18px;
}

.login_button {
    text-align: center;
    justify-content: center;
    /* margin: 40px 150px; */
    /* margin-left: 35%; */
    margin-top: 30px;
    padding: 10px 30px;
    font-size: 25px;
    border: none;
    border-radius: 30px;
    background-image: linear-gradient(to left, #43ea80, #38f8d4);
    color: white;
}



.login_button:hover {
    background-image: none;
    background-color: black;
    color: white;
    cursor: pointer;
}

.red{
    border:2px solid red;
}
.redtext{
    color:red;
    text-transform: capitalize;
}
.green{
    border:2px solid rgb(3, 255, 116);
}

input:focus{
    border:none;
}

@media only screen and (max-width: 1100px) {
    .loginbox {
        width: 550px;
    }

    .rightpanel {
        margin: 50px;
        width: 100%;
    }

    .leftpanel {
        display: none;
    }
}

@media only screen and (max-width:600px) {
    .loginbox {
        width: 350px;
    }
}