
.testDiv{
    display:flex;
    flex-direction:column;
    justify-content:center;
}

.testTitle{
    margin: 10px auto;
}

.resultsTable{
    margin:auto;
    width:70%;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 5px;
}

.correct_story{
    margin:auto;
    padding:5px 10px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        border-radius: 5px;
}


.remarks{
    text-align: center;
    margin-top: 5px auto;
}

.resultsTable tr:nth-child(even){
    background-color:rgb(241, 241, 241);
    
}
.resultsTable td{
    padding:10px 20px;
}

@media only screen and (max-width:768px){
    .resultsTable{
        width:90vw;
    }
        .resultsTable td{
            padding:5px;
        }
}