.questionsContainer {
    background-color: rgb(245, 245, 245);
    border-radius: 30px;
    margin: 20px auto;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 40%;
}

.questions {
    font-size: 60px;
    text-transform: uppercase;
    font-weight: 800;
    text-align: center;
}


.hint {
    color: rgb(100, 100, 100);
    text-align: center;
}


.largebutton {
    background-image: -webkit-linear-gradient(right, #43ea80, #17fa7d);
    border: none;
    margin: 10px;
    font-size: 35px;
    font-weight: bold;
    padding: 10px;
    border-radius: 30px;
    color: white;
}

.btnContainer {
    margin: auto;
}

.speachbutton {
    background-image: -webkit-linear-gradient(right, #43ea80, #17fa7d);
    border: none;
    margin: 10px;
    font-size: 15px;
    font-weight: bold;
    padding: 10px;
    border-radius: 30px;
    color: white;

}

.speachbutton:hover,
.largebutton:hover {
    cursor: pointer;
    background-image: none;
    background-color: black;
    color: white
}

.introbutton:disabled {
    background-image: none;
    background-color: grey;
}

.transcript {
    text-align: center
}

@media only screen and (max-width: 1100px) {
    .questionsContainer {
        margin: 10px auto;
        width: 80%;
    }
}