*{
    margin:0;
}
.parent {
    display: flex;
    background-image: url('../images/home_background.png');
    background-size: cover;
}


.body {
    margin: 75px 50px;
    display: flex;
    flex-direction: column;
    width: 500px;

}

.welcome {
    font-size: 3em;
    text-transform: uppercase;
    color: #ffffff;
    background-color: black;
    padding: 10px;
    margin: -5px;
    font-family: 'Poppins';
    font-weight: 600;
}

.title {
    font-size: 10em;
    /* color:#014227; */
    color: black;
    margin: 0px;
    font-family: 'Poppins';
    font-weight: 900;
    margin-top:-10px;
}

.headline {
    font-size: 30px;
    color: #aaaaaa;
    text-transform: uppercase;
    width: 400px;
    font-family: 'Poppins';
    font-weight: 600;
    margin-top:-30px;
}

.getstart {
    text-align: center;
    justify-content: center;
    margin: 40px 150px;
    padding: 10px;
    font-size: 25px;
    font-weight: 700;
    border: none;
    border-radius: 30px;
    background-image: linear-gradient(to left, #43ea80, #38f8d4);
    color: white;
}

.getstart:hover {
    /* color:black; */
    cursor: pointer;
    background-color: black;
    background-image: none;
}

@media only screen and (min-width:600px ) and ( max-width: 1100px) {
    .parent {
        background-image: none;
    }

    .body {
        margin: 10px;
        /* width: 200px; */
    }

    .welcome {
        font-size: 25px;
    }

    .title {
        font-size: 120px;
    }

    .headline {
        font-size: 20px;
        margin-top: -15px;
    }

    .getstart {
        font-size: 25px;
        left: 30%;
        position: relative;
    }
}


@media only screen and (min-width:350px) and (max-width: 600px) {
    .parent {
        background-image: none;
    }

    .body {
        margin: 10px;
        /* width: 200px; */
    }

    .welcome {
        font-size: 25px;
    }

    .title {
        font-size: 80px;
    }

    .headline {
        font-size: 20px;
        margin-top: -15px;
    }

    .getstart {
        font-size: 25px;
        left: 30%;
        position: relative;
    }
}

@media only screen and (max-width: 350px){
    .parent{
        background-image: none;
    }
    .body{
        margin: 10px;
        width:200px;
    }
        .welcome{
        font-size:20px;
    }
    .title{
        font-size:50px;
    }
    .headline{
        font-size: 13px;
        margin-top:-15px;
    }
    .getstart{
        font-size: 25px;
        margin:30px auto;
        left:30%;
        position:relative;
    }
}