

.resetContainer{
    margin:100px;

}

.resetContainer form input{
    display:block;
    width:90%;
    padding:10px 20px;
    margin:20px 0px;
    font-family: 'poppins';
    font-size: 20px;
}


.resetContainer form button{
    text-align: center;
        justify-content: center;
        padding: 10px 30px;
        font-size: 25px;
        font-weight: 600;
        border: none;
        border-radius: 30px;
        /* background-image: linear-gradient(to left, #43ea80, #38f8d4); */
        background: -webkit-linear-gradient(right, #43ea80, #17fa7d);
        color: white;
}

.resetContainer form button:hover{
    background-image: none;
    background-color: black;
    color: white;
    cursor: pointer;
}

.error{
    color:red;
}


@media only screen and (max-width:500px){
    .resetContainer{
        margin:20px;
    }
}