
.Navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0px 50px 50px;
}
.logo{
    width:300px;
}

button {
    font-family: 'Poppins';
}

.hamburger{
    /* width:90px; */
    font-size: 40px;
    color:white;
    display:none;
}

.hamburger:hover{
    color:black;
    cursor:pointer;
}
.leftNav {
    width: 40vw;
    height: 40px;
    background-image: linear-gradient(to right, #43ea80, #38f8d4);
    padding: 20px;
    /* padding-right:20px; */
    border-radius: 50px 0px 0px 50px;
    align-items: center;
    justify-content: flex-start;
    display: flex;
    /* flex-shrink: 5; */
    font-family: 'Poppins';
    font-size: 20px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.ourGoal {
    font-family: 'Poppins';
    font-weight: 600;
    text-decoration: none;
    color: white;
    padding: 0px 10px;
    /* margin:20px; */
}

.ourGoal:hover {
    color: black;
}

.navButtons {
    padding: 10px 30px;
    border: none;
    border-radius: 30px;
    margin: 10px;
    font-family: 'Poppins';
    font-size: 20px;
    font-weight: bold;
    background-color: white;
    cursor: pointer;
    text-transform: uppercase;
}

.navButtons:hover {
    background-color: rgb(0, 0, 0);
    color: rgb(255, 255, 255);
}

.toggled{
    display:flex;
    flex-direction: column;
    position: absolute;
    text-align: right;
    top:90px;
    z-index: 10;
    /* left:90%; */
    right: 0px;
    background-image: linear-gradient(to right, #43ea80, #38f8d4);
    border-radius: 50px;
    font-family: 'Poppins';
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding:20px 40px;
    transition:all 0.5 ease-in-out;
}

@media (max-width: 1100px) {
    .leftNav{
        margin:0;
        justify-content:center;
    }
    .innerleftnav{
        width:15vw;
        height:3vh;
        justify-content:center;
        display:none;
    }
    .hamburger{
        display:block;
    }
    .logo{
        width:200px;
    }
    .Navbar{
        padding: 10px 0px 10px 10px;
    }
    .navButtons{
        margin:10px 0px;
    }
}