
.studentDashboard{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.links{
    padding:20px;
    background-image: linear-gradient(to left, #43ea80, #38f8d4);
    text-decoration: none;
    margin:10px;
    font-weight: 800;
    font-size: 30px;
    color:white;
    border:none;
    border-radius:50px;
}

.links:hover{
    background-image: none;
    background-color:black;
}