.sentenceContainer {
    background-color: rgb(230, 230, 230);
    border-radius: 30px;
    margin: 10px 500px;
    padding:20px;
}

.sentence {
    font-size: 20px;
}



.largebutton {
    background-image: linear-gradient(to left, #43ea80, #38f8d4);
    border: none;
    margin: 10px;
    font-size: 35px;
    font-weight: bold;
    padding: 10px;
    border-radius: 30px;
    color: white;
}


.speachbutton {
    background-image: linear-gradient(to left, #43ea80, #38f8d4);
    border: none;
    margin: 10px;
    font-size: 15px;
    font-weight: bold;
    padding: 10px;
    border-radius: 30px;
    color: white;
}

.speachbutton:hover,
.largebutton:hover {
    cursor: pointer;
    background-image: none;
    background-color: black;
    color: white
}

.introbutton:disabled {
    background-image: none;
    background-color: grey;
}

.score_li{
    list-style-type:none;
}

@media only screen and (max-width: 1100px) {
    .sentenceContainer {
        margin: 10px 20px;
    }
}