

.centerHeading{
    font-size: 60px;
    text-align: center;
}

.search{
    /* float:right; */
    display: inline-block;
    margin-left: 100px;
    border:none;
    outline:0;
    border-bottom: 1px solid black;
    font-size:20px;
    padding:10px;
}

.search:focus{
    border-bottom: 2px solid black;
}

.cardsDiv{
    display: flex;
    justify-content: center;
    align-items:stretch;
    flex-wrap: wrap;
    margin:5px;
}

.card{
    background-color: rgb(252, 252, 252);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    width:26.6%;
    margin:30px auto;
    padding:30px;
    border-radius: 10px;

}

.card:hover{
    background-image: linear-gradient(to left, #43ea80, #38f8d4);
    color:white;
    cursor: pointer;
}

.name{
    text-transform: uppercase;
}

.bold{
    font-weight: bold;
}


@media only screen and (max-width:1100px){
    .card{
        width:40%;
    }
}

@media only screen and (max-width:600px) {
    .centerHeading{
        font-size: 40px;
    }
    .cardsDiv{
        margin:0;
    }
    .card {
        width: 80%;
        margin:3px;
        padding:10px;
        margin:5px;
    }
    .card p{
        font-size:13px;
    }
    .name{
        font-size:25px;
    }
    .search{
        margin-left: 5px;
    }
}